<template>
    <div class="mainList">
        <van-pull-refresh v-model="isReloading" @refresh="onRefresh">
            <van-cell
                v-for="(item, index) in list"
                :key="index"
                :title="item.title"
                @click="itemClick(item)"
            />
        </van-pull-refresh>
    </div>
</template>
<script>
import Vue from 'vue';
import { PullRefresh, Cell } from 'vant';
Vue.use(Cell).use(PullRefresh);

export default {
  components: {
  },
  data() {
    return {
      list: [],
      isReloading: false
    };
  },
  created() {
    this.loadData();
  },
  methods: {
    onRefresh() {
      this.loadData();
    },
    loadMore() {
      console.log("loadMore");
    },
    loadData() {
      return this.request
        .find("Data", {
          where: '{"type":"webapp_find"}',
          limit: "100",
          order: "index"
        })
        .then(res => {
          this.isReloading = false;
          this.list = res;
        })
        .catch(e => {
          this.isReloading = false;
          alert(e);
        });
    },
    itemClick(item) {
      if (item.route) {
        this.$router.push(item.route);
      } else if (item.url) {
        window.open(item.url);
      }
    }
  }
};
</script>
